import AuthInput from 'components/AuthInput.jsx'
import style from './UserLoginPage.module.scss'
import { useContext, useEffect, useState } from 'react'
import logo from 'assets/icons/logo.svg'
import { Link } from 'react-router-dom'
import Swal from 'sweetalert2';

function RegisterPage() {
  const [account, setAccount] = useState('');
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [recheckPassword, setRecheckPassword] = useState('');

  function handleSubmit(e){
    e.preventDefault();
    if (
      !account.trim() ||
      !name.trim()||
      !email.trim() ||
      !password.trim() ||
      !recheckPassword.trim()
    )
    {
      Swal.fire({
        position: 'top',
        title: '',
        timer: 1500,
        icon: 'error',
        showConfirmButton: false,
      });
      return
    }
    if (account.length > 50 || name.length > 50) {
      return;
    }
    if(password !== recheckPassword) {
      Swal.fire({
        position: 'top',
        title: '',
        timer: 1500,
        icon: 'error',
        showConfirmButton: false,
      });
      return
    }

    //api
    console.log(account,name, email, password, recheckPassword)
    // 
    // 
  }

  return (
    <div className={style.form}>
      <img src={logo} alt="" />
      <h1 className={style.title}></h1>
      <form onSubmit={handleSubmit}>
        <AuthInput
          label=""
          type="text"
          value={account}
          name={account}
          placeholder=""
          onChange={(e) => {
            setAccount(e.target.value)
          }}
          />
        <AuthInput
          label=""
          type="text"
          value={name}
          name={name}
          placeholder=""
          onChange={(e) => {
            setName(e.target.value)
          }}
        />
        <AuthInput
          label="Email"
          type="email"
          value={email}
          name={email}
          placeholder="Email"
          onChange={(e) => {
            setEmail(e.target.value)
          }}
        />
        <AuthInput
          label=""
          type="password"
          value={password}
          name={password}
          placeholder=""
          onChange={(e) => {
            setPassword(e.target.value)
          }}
        />
        <AuthInput
          label=""
          type="password"
          value={recheckPassword}
          name={recheckPassword}
          placeholder=""
          onChange={(e) => {
            setRecheckPassword(e.target.value)
          }}
        />
        <button  className={style.btn}></button>
      </form>
      <div className={style.linkCancel}>
        <span>
          <Link to="/login" className={style.linkItem}></Link>
        </span>
      </div>
    </div>
  )
}

export default RegisterPage